
// import React from "react";
import React, { Component } from "react";

 import Slider from 'infinite-react-carousel';
 import loadingImg from "../images/0_new/06-11-2020-110610.png";
 
 
 

  export default class myslider extends Component {

  state = {
    articles: [
      {
       title: "    ",
        text: `             ,     ,     ,       ,  ,     .          150-200 .  ,             ,    .  `,
        urlImg: loadingImg,
      },

      {
        title: "    ",
         text: `             ,     ,     ,       ,  ,     .          150-200 .  ,             ,    .  `,
         urlImg: loadingImg,
       },
       {
        title: "    ",
         text: `             ,     ,     ,       ,  ,     .          150-200 .  ,             ,    .  `,
         urlImg: loadingImg,
       },
       {
        title: "    ",
         text: `             ,     ,     ,       ,  ,     .          150-200 .  ,             ,    .  `,
         urlImg: loadingImg,
       },
       {
        title: "    ",
         text: `             ,     ,     ,       ,  ,     .          150-200 .  ,             ,    .  `,
         urlImg: loadingImg,
       },

     
    ]
  };


  render() {

    const settings =  {
      autoplay: true,
      adaptiveHeight: true,
     
    };

  return  (
  <>
 

 <Slider { ...settings } dots>

 {this.state.articles.map((item, index) => {
            return (
 

              <div key={index}>
              <div className="ItemSlider">
                
              <div className="divTitle">
              <h3>{item.title}</h3>
              </div>
        
              <div className="divSubTitle">
              <p>{item.text}
              </p>
              </div>
        
         <div className="divRight">
            <img className="imgRight" src={item.urlImg} alt="" />
         </div>
             </div>
            </div>


            );
            
          })}




 
  </Slider>

</>
)
        }

};
 






 
  